body {
  background: $color-bg-main;
  color: $color-text-main;
  @include f(t, 15, 21);
  @include smooth-font;
  min-width: $layout-width + 40px;
  &.modal-open {
    overflow: hidden; } }

b, strong {
  @include f(b); }

a {
  color: $color-blue;
  &:not(:hover) {
    text-decoration: none; } }

.l {
  max-width: $layout-width + px;
  margin: auto;
  padding: 0 20px;
  box-sizing: content-box; }

.l-row {
  display: flex;
  align-items: stretch;
  align-content: stretch;
  flex-wrap: nowrap; }

.l-col {
  box-sizing: border-box; }

.l-cell {
  width: 100%;
  box-sizing: border-box; }

.l-multicolumn {
  display: flex;
  align-items: stretch;
  justify-content: space-between; }

.dropdown {
  position: relative; }

.icon {
  fill: currentColor; }

.text-important {
  color: $color-text-important; }

.text-green {
  color: $color-text-green; }
